.drop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  opacity: 0.3;
}

.form {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 2rem;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2rem;
}

.form__wrapper {
  width: 50%;
  margin: 0 auto;
}

.form__title {
  font-size: 2rem;
  font-weight: 200;
  text-align: center;
}

.form__contact-data {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}

.form__label {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.form input,
.form textarea {
  margin-top: 0.5rem;
  border: none;
  border-radius: 5px;
  font-family: Arial, Helvetica, sans-serif;
}

.form input[type=text],
.form textarea,
.form button {
  box-shadow: 2px 1px 3px 1px rgba(89, 87, 87, 0.5);
}

.form input {
  width: 20rem;
  height: 3rem;
}

.form textarea::-webkit-input-placeholder,
.form input::-webkit-input-placeholder {
  font-family: Arial, Helvetica, sans-serif;
}

.form textarea::-moz-placeholder,
.form input::-moz-placeholder {
  font-family: Arial, Helvetica, sans-serif;
}

.form__button {
  display: flex;
  justify-content: center;
  align-items: center;
}

.form__button button {
  width: 15rem;
  height: 3rem;
  margin-top: 0.5rem;
  background-color: #96a19b;
  border: 1px solid white;
  border-radius: 5px;
  color: white;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
}

.form button:hover {
  cursor: pointer;
  background-color: white;
  color: #96a19b;
}

@media (max-width: 89rem) {
  .form__contact-data {
    display: flex;
    flex-flow: column;
    align-items: flex-start;
  }
}

@media (max-width: 78rem) { 
  .form__wrapper {
    width: 70%;
  }

  .form__button button {
    font-size: 1rem;
  }
}

@media (max-width: 32rem) { 
  .form__wrapper {
    width: 80%;
  }

  .form__title {
    margin: 0 0 2rem;
    font-size: 1.5rem;
    text-align: center;
  }

  .form__label {
    margin: 0.5rem;
  }

  .form input,
  .form textarea,
  .form button {
    margin: 0;
  }

  .form input {
    width: 12rem;
    height: 2rem;
  }

  .form__button button {
    width: 8rem;
    height: 2rem;
    font-size: 0.9rem;
  }
}

@media (max-height: 24rem) and (orientation: landscape) {
  .form {
    margin: 0 0 1rem;
  }

  .form__wrapper {
    width: 90%;
    height: 100vh;
  }

  .form__title {
    font-size: 1.5rem;
  }
  
  .form__contact-data {
    display: flex;
    flex-flow: row;
  }

  .form__wrapper input,
  .form__wrapper button {
    height: 2rem;
  }

  .form__wrapper textarea {
    height: 20vh;
  }

  .form__wrapper label {
    margin: 0.5rem;
  }
} 