main {
  scroll-snap-align: start;
}

.main {
  display: flex;
  flex-direction: column;
  scroll-snap-type: y mandatory;
}

.main__content {
  width: 100%;
  height: 100vh;
  top: 0;
  scroll-snap-align: start;
}

.main__slider-wrapper {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

.slide {
  height: 100vh;
  background-size: cover !important;
}

.slide::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.9)));
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
    bottom: 0;
    left: 0;
}

.previousButton, .nextButton {
		position: absolute;
		top: 50%;
		width: 2rem;
		height: 2rem;
    transform: translateY(-50%);
		z-index: 10;
    opacity: 0.4;
    cursor: pointer;
}

.previousButton {
  left: 10px;
  -webkit-transform: rotate(0deg) translateY(calc(50% - 50px));
          transform: rotate(0deg) translateY(calc(50% - 50px));
}

.nextButton {
  right: 0;
  -webkit-transform: rotate(0deg) translateY(calc(50% - 50px));
          transform: rotate(0deg) translateY(calc(50% - 50px));
}

.main__slider-content {
  text-align: center;
}

.main__slider-content .main__slider-inner {
  padding: 0 4.5rem;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 10rem;
  bottom: 0;
  left: 50%;
  overflow-y: auto;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.main__slider-content .main__slider-inner p {
  margin: 1.25rem auto 1.875rem;
  max-width: 60rem;
  line-height: 1.5;
  color: #FFFFFF;
  font-family: "Great Vibes", cursive;
  font-size: 1.5rem;
}

.slide p {
	transition: all 0.3s ease;
	-webkit-transform: translateY(20px);
					transform: translateY(20px);
	opacity: 0;
}

.slide.animateIn.previous p,
.slide.current p,
.slide.animateIn.next p {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	-webkit-transition-delay: .9s;
					transition-delay: .9s;
					opacity: 1;
}

.slide.animateIn.previous p,
.slide.animateIn.next p {
	-webkit-transition-delay: 1.1s;
					transition-delay: 1.1s;
}

.slide.animateOut p {
	-webkit-transition-delay: .2s;
					transition-delay: .2s;
}

.main__form {
  background-color: #96a19b;
  scroll-snap-align: start;
}

.main__advertising {
  position: relative;
  height: 30vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: white;
  color: #96a19b;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  text-align: center;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 10px grey; 
  border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
  background: rgba(0,0,0,0.5);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.6); 
}

@media (max-width: 36rem) { 
  .main__content,
  .main__slider-wrapper,
  .slide {
    height: 50vh;
  }

  .main__slider-content .main__slider-inner {
    padding: 0 1.25rem;
    height: 5rem;
  }

  .main__slider-content .main__slider-inner p {
    margin: 0.625rem auto 1.25rem;
    line-height: 1.2;
    font-size: 1.2rem;
  }
}
