.header {
  height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  background: center/100% no-repeat url("../../assets/images/nc.jpg");
  background-size: cover;
  background-attachment: fixed;
}

header:after {
  content: "";
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.7));
  z-index: 1;
}

.header__title,
.header__subtitle {
  color: white;
  text-align: center;
  z-index: 10;
}

.header__title {
  font-family: "Abril Fatface", cursive;
  font-size: 4.5rem;
  font-weight: 400;
}

.header__subtitle {
  font-family: "Great Vibes", cursive;
  font-size: 2.5rem;
}

@media (max-width: 78rem) { 
  .header__title {
    padding: 0 3rem;
    font-size: 3.5rem;
  }

  .header__subtitle {
    padding:  0 1rem;
    font-size: 2rem;
  }
}

@media (max-width: 36rem) { 
  .header,
  header:after {
    height: 50vh;
  }
 
  .header__title {
    font-size: 2.5rem;  
  }

  .header__subtitle {
    font-size: 1.5rem;
  }
}
