footer {
  scroll-snap-align: start;
}

.footer {
  width: 100%;
  background-color: #96a19b;
  color: white;
  text-align: center;
}

.footer__title {
  font-family: "Great Vibes", cursive;
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 2px;
}

.footer__subtitle {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 0.8rem;
  font-weight: 200;
}

@media (max-width: 78rem) { 
  .footer__title {
    font-size: 1rem;
  }

  .footer__subtitle {
    font-size: 0.6rem;
  }
}
